.newProduct{
    flex: 4;
    padding: 20px;
}
.newProductTitle{
    margin-bottom: 20px;
}
.newProductForm{
    width: 250px;
    display: flex;
    flex-direction: column;
}
.newProductForm>label{
    margin-bottom: 10px;
    color: gray;
}
.newProductForm>input{
    margin-bottom: 20px;
    padding: 10px;
}
.newProductForm>select{
    margin-bottom: 20px;
    padding: 10px;
}
.newProductBtn{
    width: 80px;
    padding: 5px;
    border: none;
    background-color: darkblue;
    border-radius: 5px;
    color: white;
    font-weight: 600;
    cursor: pointer;
}