.product{
    flex:4;
    padding: 20px;
}
.productTitleContainer{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.productAddBtn{
    border: none;
    background-color: green;
    padding: 7px 10px;
    color: white;
    border-radius: 5px;
    width: 80px;
    font-size: 16px;
    cursor: pointer;
}
.productTop{
    display: flex;
}
.productTopLeft,
.productTopRight{
    flex:1;
}
.productTopRight{
    padding: 20px;
    box-shadow: 0 0 25px -10px rgb(94, 94, 94);
    border-radius: 10px;
    margin: 20px;
}
.productTopImg{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    object-fit: cover;
    margin-right: 10px;
}
.productTopInfo{
    display: flex;
    align-items: center;
}
.productTopName{
    font-weight: 600;
}
.productInfoBottom{
    margin-top: 10px;
}
.productInfoItem{
    width: 150px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.productInfoValue{
    font-weight: 300;
}
.productBottom{
    padding: 20px;
    box-shadow: 0 0 25px -10px rgb(94, 94, 94);
    border-radius: 10px;
    margin: 20px;
}
.productForm{
    display: flex;
    justify-content: space-between;
}
.productFormLeft{
    display: flex;
    flex-direction: column
}
.productFormLeft>label{
    margin-bottom: 10px;
    color: gray;
}
.productFormLeft>input{
    margin-bottom: 10px;
    border: none;
    padding: 5px;
    border-bottom: 1px solid gray;
}
.productFormLeft>select{
    margin-bottom: 10px;
}
.productUploadImg{
    width: 100px;
    height: 100px;
    border-radius: 10px;
    object-fit: cover;
    margin-right: 20px;
}
.productFormRight{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}
.productUpload{
    display: flex;
    align-items: center;
}
.productButton{
    border: none;
    padding: 5px;
    background-color: darkblue;
    color: white;
    cursor: pointer;
    font-weight: 600;
    border-radius: 5px;
}