.widgetLarge{
    flex: 2;
    box-shadow: 0 0 25px -10px rgb(94, 94, 94);
    border-radius: 10px;
    padding: 20px;
    margin: 20px;
}
.widgetLargeTitle{
    font-size: 22px;
    font-weight: 600;
}
.widgetLargeTable{
    width: 100%;
    border-spacing: 20px;
}
.widgetLargeTh{
    text-align: left;
}
.widgetLargeUser{
    display: flex;
    align-items: center;
    font-weight: 600;
}
.widgetLargeImg{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    object-fit: cover;
    margin-right: 10px;
}
.widgetLargeDate,
.widgetLargeAmt{
    font-weight: 300;
} 
.widgetLargeBtn{
    padding: 5px 7px;
    border: none;
    border-radius: 10px;
}
.widgetLargeBtn.Approved{
    background-color: #e5faf2;
    color: #0d8b5b;
}
.widgetLargeBtn.Declined{
    background-color: #f8c9c8;
    color: #f76f6d;
}
.widgetLargeBtn.Pending{
    background-color: #d4e7f8;
    color: #4aa9fd;
}
