.productsList{
    flex: 4;
}
.productListProduct{
    display: flex;
    align-items: center;
}
.productListImg{
    width: 32px;
    height: 32px;
    border-radius: 50%;
    object-fit: cover;
    margin-right: 10px;
}
.productListEdit{
    border: none;
    padding: 5px 10px;
    border-radius: 10px;
    background-color: green;
    color: white;
    cursor: pointer;
    margin-right: 20px;
}
.productListDelete{
    color: red;
    margin-left: 10px;
    cursor: pointer;
}